<template>
    <div class="upass-container">
        <v-dialog
            v-model="passDialogShow"
            width="350"
            persistent
            rounded
            style="z-index:100"
        >
            <div class="modal-layout" style="height:300px;background: #fff;" >
                <form novalidate  @submit.prevent="validate">
                <div class="pass-title">비밀번호 입력</div>
                <div class="input-box" style="width:100%;" v-if="passDialogShow">
                    <v-text-field v-model="userPass"
                                  :rules="[() => !!userPass || '비밀번호를 입력하세요']"
                                  required
                                  rounded
                                  outlined
                                  ref="userPass"
                                  type="password"
                                  maxLength="10">

                    </v-text-field>
                </div>
                <div class="button-row">
                    <v-btn style="width:120px;margin:auto;" @click="passDialogShow=false" class="whiteButton" depressed rounded type="button"><span>취소</span></v-btn>
                    <v-btn style="width:120px;margin:auto;" class="blackButton" depressed rounded type="submit"><span>확인</span></v-btn>
                </div>
                </form>
            </div>
        </v-dialog>
    </div>
</template>
<script>

import AUTH from '@/config/web/auth'
import apiService from '@/config/web/apiService'
import {
    API_URL_CHECK_BOARD_ARTICLE_UPASS
} from '@/config/web/urls'

export default {
    name:'articleUpass',
    props:["bid","articleId","modelOpen","type","mcode","scode"],
    watch:{
        modelOpen(val) {
            this.userPass = '';
            if(val) {
                this.passDialogShow = val;
                this.$emit('parentOpenFalse',{})
            }

        }
    },
    data() {
        return {
            passDialogShow:false,
            userPass:'',
        };
    },
    created() {
        this.userPass = '';
    },
    methods: {
        articelView(item) {
            this.articleId = item.id;
            this.passDialogShow = true;

        },
        validate() {
            if (!this.userPass) {
                this.$refs.userPass.validate(true)
                return;
            }
            const frm = {id: this.articleId, userPass: this.userPass}
            this.$eventBus.$emit('overlay', 'open');
            apiService.post(API_URL_CHECK_BOARD_ARTICLE_UPASS, frm, (res) => {
                this.$eventBus.$emit('overlay', 'hide');
                if (res.status == 'success') {
                    sessionStorage.setItem('articleSessPass', res.sessPass)
                    this.passDialogShow = false;
                    if (this.type == 'modify') this.$router.push('/board/articleRegist/' + this.mcode + '/' + this.scode + '/' + this.bid + '/' + this.articleId)
                    else {
                        this.$emit('upassSuccess', {})
                    }

                } else {
                    let params = {
                        message: '잘못된 비밀번호 입니다',
                    };
                    this.$eventBus.$emit('modalOpen', params);
                }

            })
        },
    }

}
</script>

<style lang="scss" scoped>
.board-container {
    width: 100%;
    margin: 50px 0;
    min-height:calc(100vh - 300px);

}
.modal-layout {
    padding:30px;
    .pass-title {
        font-siZe:18px;
        font-weight: bold;
        color:#000;
        padding:20px 0 30px 0;
        text-align:center;
    }
    .input-box {

    }
    .button-row {
        display: flex;
        justify-content: space-between;
        .whiteButton {
            min-height:40px;
            span {
                font-size:14px;
            }
        }
        .blankButton {
            min-height:40px;
            max-height:40px !important;
            span {
                font-size:14px;
            }
        }
    }
}
</style>