<template>
    <div class="comment-container" id="comment-base">
        <div class="comment-title">댓글 ({{ total }})</div>

        <div class="form-box">
            <div class="input-box" id="c">
                <v-textarea ref="comment" v-model="comment" outlined dense rows="2" placeholder="댓글을 입력하세요"
                            maxLength="250" no-resize
                            :rules="[()=>!!comment || '댓글을 입력하세요']"/>
            </div>
            <div v-if="commentId" class="button-box">
                <v-btn class="blackButton" @click="updateReview()" depressed><span>수정</span>
                </v-btn>
                <v-btn class="whiteButton" @click="reset()" depressed><span>취소</span>
                </v-btn>
            </div>
            <v-btn v-else class="blackButton" @click="updateReview()" depressed><span>등록</span>
            </v-btn>
        </div>
        <div class="comment-box">
            <ul class="main-comment-ul">
                <li v-for="(item,index) in commentList" :key="item.id" :id="'comment_'+item.id">
                    <div class="main-comment">
                        <div class="user-img-box">
                            <img v-if="item.memImg" :src="item.memImg">
                            <font-awesome-icon v-else class="icon" icon="fa-solid fa-user"/>
                        </div>
                        <div class="content-box">
                            <div class="user-box">
                                <div class="uname">{{ item.userName }}</div>
                                <div class="rdate">
                                    {{ item.createAt.substring(0, 16).replace('T',' ')}}

                                </div>
                            </div>
                            <div class="content" v-html="item.content">

                            </div>

                        </div>
                        <div class="modify-box">
                            <div class="modify-button-icon" v-if="item.modifyButton" @click="showModifyBox(index)">
                                <font-awesome-icon icon="fa-solid fa-ellipsis-vertical"/>
                            </div>
                            <div class="modify-button-box" v-if="item.modifyButtonShow">
                                <div class="modify-button-text" @click="setModify(index)">
                                    수정
                                </div>
                                <div class="modify-button-text" @click="deleteComment(index)">
                                    삭제
                                </div>
                            </div>

                            <div class="reply-action">
                                <v-btn class="whiteButton" @click="showRepleBox(index)" depressed><span>답글</span>
                                </v-btn>
                            </div>
                        </div>
                    </div>
                    <div class="sub-comment" v-if="item.subList && item.subList.length>0 || item.repleShow">
                        <ul class="sub-comment-ul">
                            <li v-for="(subItem,subIndex) in item.subList" :key="subItem.id"
                                :id="'comment_'+subItem.id">
                                <div class="arrow-img">
                                    <img src="../../../assets/img/img_rp.svg">
                                </div>
                                <div class="user-img-box">
                                    <img v-if="subItem.memImg" :src="subItem.memImg">
                                    <font-awesome-icon v-else class="icon" icon="fa-solid fa-user"/>
                                </div>
                                <div class="content-box">
                                    <div class="user-box">
                                        <div class="uname">{{ subItem.userName }}</div>
                                        <div class="rdate">
                                            {{ subItem.createAt.substring(0, 16).replace('T',' ')}}

                                        </div>
                                    </div>
                                    <div class="content" v-html="subItem.content">

                                    </div>

                                </div>
                                <div class="modify-box">
                                    <div class="modify-button-icon" @click="showSubModifyBox(index,subIndex)"
                                         v-if="subItem.modifyButton">
                                        <font-awesome-icon icon="fa-solid fa-ellipsis-vertical"/>
                                    </div>
                                    <div class="modify-button-box" v-if="subItem.modifyButtonShow">
                                        <div class="modify-button-text" @click="setSubModify(index,subIndex)">
                                            수정
                                        </div>
                                        <div class="modify-button-text" @click="deleteSubComment(index,subIndex)">
                                            삭제
                                        </div>
                                    </div>

                                </div>
                            </li>
                        </ul>
                        <div class="form-box" v-if="item.repleShow">
                            <div class="input-box">

                                <v-textarea  :error-messages="subCommentError[index]"
                                            v-model="subComment[index]" outlined dense rows="2" placeholder="답글을 입력하세요"
                                            maxLength="250" no-resize @input="subCommentErrorCheck(index)"
                                            @blur="subCommentErrorCheck(index)"/>
                            </div>
                            <div v-if="subCommentUpdate[index]" class="button-box">
                                <v-btn class="blackButton" @click="updateSubReview(index)" depressed><span>수정</span>
                                </v-btn>
                                <v-btn class="whiteButton" @click="subReset(index)" depressed><span>취소</span>
                                </v-btn>
                            </div>
                            <v-btn v-else class="blackButton" @click="updateSubReview(index)" depressed>
                                <span>등록</span>
                            </v-btn>
                        </div>

                    </div>
                </li>
            </ul>
        </div>
        <div v-if="scrolling" style="text-align: center;padding:200px 0;">
            <v-progress-circular
                    :size="80"
                    color="#AD1457"
                    indeterminate
            ></v-progress-circular>
        </div>

    </div>
</template>
<script>

    import AUTH from '@/config/web/auth'
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_COMMENT_LIST,
        API_URL_UPDATE_COMMENT,
        API_URL_INSERT_COMMENT,
        API_URL_DELETE_COMMENT,
    } from '@/config/web/urls'
    import UTIL from '@/common/util'

    import JQ from "jquery";
    import {mapActions, mapGetters} from "vuex";

    export default {
        name: 'commentList',
        props: [
            "ptype",
            "pid",
            "boardId",
            "openType",
        ],
        computed: {
            ...mapGetters('config', ["getListScrollMore"]),
            form() {
                return {
                    comment: this.comment,
                }
            },
        },
        watch: {
            getListScrollMore(val) {
                if (val) {
                    this.handleLoadMore();

                }
            }
        },
        data() {
            return {

                limit: 10,
                page: 1,
                lastPage: 1,
                total: 0,
                memberImg: '',
                isLogin: false,
                commentList: [],
                comment: '',
                commentId: '',
                memberId: '',
                insertFlag: false,
                formHasErrors: false,
                subComment: [],
                subCommentError: [],
                subCommentUpdate: [],
                subCommentId: '',
                commentIndex: '',
                auth: "user",
                scrolling: false,
                isAdmin: false,
                modifyBoxShow: false,
                scrollBase: 'base',
            };
        },
        created() {
            this.$eventBus.$on('bodyClick', this.modifyCheck)
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.memberId = member.memberInfo.uid;
                this.isAdmin = (member.memberInfo.role == 'ROLE_ADMIN') ? true : false;
                this.isLogin = true;
            }
            this.scrollBase = (this.openType == 'dialog') ? '#articleView-container' : 'base';

            this.getData();
        },
        methods: {
            ...mapActions('config', ["setListScroll", "setListScrollMore"]),
            getData() {
                this.scrolling = true;
                const frm = this.getSearchParams();
                apiService.post(API_URL_GET_COMMENT_LIST, frm, (res) => {
                    if (res.status == 'success') {
                        this.commentList = [];
                        this.total = UTIL.numberFormat(res.commentData.total);
                        this.setCommentList(res.commentData.list)
                        if (this.isLogin) {

                        }
                    }
                })
            },
            getDataList() {
                this.scrolling = true;
                const frm = this.getSearchParams();
                apiService.post(API_URL_GET_COMMENT_LIST, frm, (res) => {
                    if (res.status == 'success') {
                        this.setCommentList(res.commentData.list)
                    }
                })
            },
            modifyCheck() {
                if (this.modifyBoxShow) {
                    this.modifyBoxShow = false;
                    return;
                }
                this.setAllHide();

            },
            setCommentList(datas) {
                let lastPage = this.total / this.limit;
                lastPage = Math.floor(lastPage) + 1;
                this.lastPage = lastPage;

                for (let item of datas) {
                    item.modifyButton = (this.isLogin && (this.memberId == item.userId || this.isAdmin)) ? true : false;
                    item.repleShow = false;
                    item.modifyButtonShow = false;
                    if (item.subList && item.subList.length > 0) {
                        for (let subItem of item.subList) {
                            subItem.modifyButton = (this.isLogin && (this.memberId == subItem.userId || this.isAdmin)) ? true : false;
                            subItem.modifyButtonShow = false;
                        }
                    }
                    this.commentList.push(item)
                }
                for (const index in this.commentList) {
                    this.subComment[index] = '';
                    this.subCommentError[index] = ''
                    this.subCommentUpdate[index] = false;
                }
                this.scrolling = false;
                if (this.page < this.lastPage) {
                    this.setListScroll(true)
                    this.setListScrollMore(false)
                } else {
                    this.setListScroll(false)
                }


            },
            showModifyBox(index) {
                this.setAllHide();
                this.modifyBoxShow = true;
                let item = this.commentList[index];
                item.modifyButtonShow = true;

            },
            showSubModifyBox(index, subIndex) {
                this.setAllHide();
                this.modifyBoxShow = true;
                let item = this.commentList[index].subList[subIndex];
                item.modifyButtonShow = true;

            },
            setAllHide() {
                for (let item of this.commentList) {
                    if (item.modifyButtonShow) item.modifyButtonShow = false;
                    if (item.subList && item.subList.length > 0) {
                        for (let subItem of item.subList) {
                            if (subItem.modifyButtonShow) subItem.modifyButtonShow = false;
                        }
                    }
                }
            },
            getSearchParams() {
                return {
                    parentType: this.ptype,
                    parentId: this.pid,
                    limit: this.limit.toString(),
                    page: this.page.toString()
                }
            },
            showRepleBox(index) {
                if (this.subCommentId) return;

                for (let key in this.commentList) {
                    if (index == key) {
                        if (this.commentList[key].repleShow) this.commentList[key].repleShow = false;
                        else this.commentList[key].repleShow = true;
                    } else {
                        this.commentList[key].repleShow = false;
                    }
                }

            },
            setModify(index) {
                if (this.subCommentId) {
                    const params = {
                        message: '기존 수정 작업을 먼저 완료하세요',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                const item = this.commentList[index];
                this.comment = UTIL.stripTags(item.content);
                this.commentId = item.id;
                if(this.scrollBase == 'base') {
                    UTIL.errorPosition(this.scrollBase, '#comment-base', () => {

                    })
                }


            },
            setSubModify(index, subIndex) {
                if (this.commentId) {
                    const params = {
                        message: '기존 수정 작업을 먼저 완료하세요',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                this.commentList[index].repleShow = true;

                const item = this.commentList[index].subList[subIndex];
                this.$set(this.subComment, index, UTIL.stripTags(item.content));
                this.subCommentId = item.id;
                this.commentIndex = index;
                for (const key in this.subCommentUpdate) {
                    this.subCommentUpdate[key] = false;
                }
                this.subCommentUpdate[index] = true;
                if(this.scrollBase == 'base') {
                    UTIL.errorPosition(this.scrollBase, '#comment_' + this.commentList[index].id, () => {

                    })
                }


            },
            deleteComment(index) {
                const params = {
                    type: 'confirm',
                    message: '댓글을 삭제하시겠습니까?',
                    doAction: () => {
                        const id = this.commentList[index].id;
                        this.blindAction(id, 'main');
                    }
                }
                this.$eventBus.$emit('modalOpen', params);

            },
            deleteSubComment(index, subIndex) {
                const params = {
                    type: 'confirm',
                    message: '답글을 삭제하시겠습니까?',
                    doAction: () => {
                        const id = this.commentList[index].subList[subIndex].id;
                        this.blindAction(id, 'sub');
                    }
                }
                this.$eventBus.$emit('modalOpen', params);

            },
            blindAction(id, type) {
                const frm = {
                    id: id,
                }
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_URL_DELETE_COMMENT, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.page = 1;
                    const msg = (type == 'sub') ? '답글' : '댓글';
                    const params = {
                        message: msg + '이 삭제되었습니다',
                        doAction: () => {
                            this.getData();
                        }
                    }
                    //this.$emit('orderReviewReload')
                    this.$eventBus.$emit('modalOpen', params);
                })
            },
            reset() {
                this.commentId = '';
                this.comment = '';
                this.$refs['comment'].reset();
            },
            subReset(index) {
                this.subCommentId = '';
                for (const key in this.subCommentUpdate) {
                    this.subCommentUpdate[key] = false;
                }
                this.subComment[index] = '';
                this.$set(this.subCommentError, index, '')
            },
            subCommentErrorCheck(index) {
                if (!this.subComment[index]) {
                    this.$set(this.subCommentError, index, '답글을 입력하세요')

                } else {
                    this.$set(this.subCommentError, index, '')
                }

            },
            updateSubReview(index) {
                if (this.insertFlag) return;
                if (this.auth == 'user') {
                    if (!this.isLogin) {
                        let params = {
                            message: '먼저 로그인 하세요',
                        };
                        this.$eventBus.$emit('modalOpen', params);
                        return;
                    }
                }
                if (!this.subComment[index]) {
                    this.$set(this.subCommentError, index, '답글을 입력하세요')
                    return;
                }

                this.insertFlag = true;
                this.$eventBus.$emit('overlay', 'open');
                const id = this.commentList[index].id;
                let url = '';
                let frm = {
                    parentId: this.pid,
                    parentType: this.ptype,
                    bid: this.boardId,
                    content: this.subComment[index],
                    pid: id,
                    depth: 2
                };
                if (this.subCommentId) {
                    url = API_URL_UPDATE_COMMENT;
                    frm.id = this.subCommentId;
                } else {
                    url = API_URL_INSERT_COMMENT;
                }

                apiService.post(url, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.insertFlag = false;
                    if (this.subCommentId) {
                        let k = 0;
                        let changeData = {};
                        for (let item of this.commentList[this.commentIndex].subList) {
                            if (item.id == this.subCommentId) {
                                item.content = UTIL.nl2brContent(this.subComment[index]);
                                changeData = item;
                                break;
                            }
                            k++;
                        }
                        this.$set(this.commentList[this.commentIndex].subList, changeData, k);
                        if(this.scrollBase == 'base') {
                            UTIL.errorPosition(this.scrollBase, '#comment_' + this.subCommentId, () => {

                            })
                        }
                    } else {
                        this.page = 1;
                    }
                    const msg = (this.subCommentId) ? '수정' : '저장';
                    const params = {
                        message: '답글이 ' + msg + ' 되었습니다',
                        doAction: () => {
                            if (!this.subCommentId) {
                                this.getData();

                            }
                        }
                    }
                    this.subReset(this.commentIndex);
                    this.commentIndex = '';
                    this.$eventBus.$emit('modalOpen', params);

                })
            },
            updateReview() {
                if (this.insertFlag) return;
                if (this.auth == 'user') {
                    if (!this.isLogin) {
                        let params = {
                            message: '먼저 로그인 하세요',
                        };
                        this.$eventBus.$emit('modalOpen', params);
                        return;
                    }
                }
                this.$refs['comment'].validate(true)
                if(!this.comment) {
                    return;
                }
                this.insertFlag = true;
                this.$eventBus.$emit('overlay', 'open');
                let url = '';
                let frm = {
                    parentId: this.pid,
                    parentType: this.ptype,
                    bid: this.boardId,
                    content: this.comment,
                    depth: 1
                };
                if (this.commentId) {
                    url = API_URL_UPDATE_COMMENT;
                    frm.id = this.commentId;
                } else {
                    url = API_URL_INSERT_COMMENT;
                }

                apiService.post(url, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.insertFlag = false;
                    if (this.commentId) {
                        let k = 0;
                        let changeData = {};
                        for (let item of this.commentList) {
                            if (item.id == this.commentId) {
                                item.content = UTIL.nl2brContent(this.comment);
                                changeData = item;
                                break;
                            }
                            k++;
                        }
                        this.$set(this.commentList, changeData, k);

                        if(this.scrollBase == 'base') {
                            UTIL.errorPosition(this.scrollBase, '#comment_' + this.commentId, () => {

                            })
                        }
                    } else {
                        this.page = 1;

                    }
                    const msg = (this.commentId) ? '수정' : '저장';
                    const params = {
                        message: '댓글이 ' + msg + ' 되었습니다',
                        doAction: () => {
                            if (!this.commentId) {
                                this.getData();

                            }
                        }
                    }
                    this.commentId = '';
                    this.comment = '';
                    this.$refs['comment'].reset();
                    this.$eventBus.$emit('modalOpen', params);

                })
            },
            handleLoadMore() {
                if (this.page < this.lastPage) {
                    this.page++;
                    this.getDataList();
                }
            }


        },
        destroyed() {
            this.$eventBus.$off('bodyClick')
            this.setListScroll(false)
        },
        updated() {
            const viewComment = sessionStorage.getItem('viewComment');
            if (viewComment) {
                UTIL.errorPosition(this.scrollBase, '#comment_' + viewComment, () => {

                })

                JQ('#comment_' + viewComment).animate({
                    opacity: 0.1,
                }, 700, function () {
                    JQ(this).animate({
                        opacity: 1,
                    }, 700)
                });
                sessionStorage.setItem('viewComment', '');
            }

        }

    }
</script>

<style lang="scss" scoped>
    .comment-container {
        width: 100%;
        margin: 30px 0;

        .comment-title {
            height: 28px;
            widht: 100%;
            line-height: 28px;
            font-size: 14px;
            font-weight: bold;
            color: #000;
            padding-left: 15px;
            background: #DCDCDC;
            margin-bottom: 20px;
        }

        .form-box {
            display: flex;
            justify-content: space-between;
            padding: 10px 0;

            .input-box {
                width: 95%;
            }

            .button-box {
                min-width: 66px;
                width: 66px;

                .blackButton {
                    margin-left: 0;
                    min-width: 66px;
                    width: 66px;
                    min-height: 30px;
                    height: 31px;
                    max-height: 31px;
                    border-radius: 5px 5px 0 0;

                    span {
                        font-size: 14px;
                    }
                }

                .whiteButton {
                    min-width: 66px;
                    width: 66px;
                    border-radius: 0 0 5px 5px;
                    min-height: 30px;
                    height: 31px;
                    max-height: 31px;
                    border: solid 1px #333;

                    span {
                        font-size: 14px;
                    }
                }
            }

            .blackButton {
                margin-left: 0;
                min-width: 66px;
                width: 66px;
                min-height: 62px;

                span {
                    font-size: 14px;
                    font-weight: normal !important;
                }
            }
        }

        .comment-box {
            .main-comment-ul {
                padding: 0;
                margin: 0;
                border-top: solid 1px #dcdcdc;

                & > li {
                    padding: 20px 0;
                    border-bottom: solid 1px #dcdcdc;

                    .main-comment {
                        display: flex;
                        justify-content: space-between;
                        padding: 0 15px;

                        .user-img-box {
                            width: 35px;
                            min-width: 35px;
                            height: 35px;
                            overflow: hidden;
                            border-radius: 17.5px;
                            background: #eee;
                            text-align: center;
                            line-height: 40px;

                            .icon {
                                font-size: 14pt;
                            }

                            img {
                                width: 35px;
                                min-height: 35px;
                            }
                        }

                        .content-box {
                            width: 98%;
                            padding-right: 20px;
                            padding-left: 15px;

                            .user-box {
                                display: flex;

                                .uname {
                                    padding-top: 3px;
                                    color: #333333;
                                    font-weight: bold;
                                    font-size: 18px;

                                }

                                .rdate {
                                    padding: 9px 0 0 15px;
                                    color: #999999;
                                    font-size: 12px;
                                }
                            }

                            .content {
                                padding-top: 8px;
                                color: #333333;
                                font-size: 15px;
                            }

                        }


                        .modify-box {
                            min-width: 50px;
                            text-align: right;
                            color: #999;
                            position: relative;

                            .modify-button-box {
                                position: absolute;
                                top: 0;
                                right: 10px;
                                width: 45px;
                                height: 50px;
                                z-index: 10;
                                background: #FFFFFF;
                                box-shadow: 0px 0px 6px #00000029;
                                border-radius: 3px;
                                text-align: center;
                                padding-top: 1px;

                                .modify-button-text {
                                    color: #333333;
                                    font-size: 12px;
                                    margin-top: 5px;
                                    cursor: pointer;
                                }

                            }

                            .modify-button-icon {
                                font-size: 18px;
                                color: #333;
                                cursor: pointer;
                            }
                        }

                        .reply-action {
                            padding-top: 10px;

                            .whiteButton {
                                width: 43px;
                                min-width: 43px;
                                height: 25px;
                                max-height: 25px !important;
                                border: #333 solid 1px;

                                span {
                                    font-size: 12px;
                                }
                            }
                        }

                    }

                    .sub-comment {
                        margin: 14px 0 0 0;
                        padding: 0;
                        background: #f4f4f4;
                        border-radius: 5px;

                        .sub-comment-ul {
                            padding: 0;
                            margin: 0;

                            li {
                                border-bottom: solid 1px #dcdcdc;

                                &:last-child {
                                    border-bottom: none;
                                }

                                display: flex;
                                justify-content: space-between;
                                padding: 20px 15px;

                                .arrow-img {
                                    padding: 10px 15px 0 8px;
                                }

                                .user-img-box {
                                    width: 35px;
                                    min-width: 35px;
                                    height: 35px;
                                    overflow: hidden;
                                    border-radius: 17.5px;
                                    background: #fff;
                                    text-align: center;
                                    line-height: 40px;

                                    .icon {
                                        font-size: 14pt;
                                    }

                                    img {
                                        width: 35px;
                                        min-height: 35px;
                                    }
                                }

                                .content-box {
                                    width: 98%;
                                    padding-right: 20px;
                                    padding-left: 15px;

                                    .user-box {
                                        display: flex;

                                        .uname {
                                            padding-top: 3px;
                                            color: #333333;
                                            font-weight: bold;
                                            font-size: 18px;

                                        }

                                        .rdate {
